<script>
    import Datepicker from 'vuejs-datepicker'
    import {
        getDDMMMYYYY,
        addDaysToDate,
        minusDaysToDate,
        formatDate,
        getTime,
        getDifference,
        ensureTwoDigits,
        formatMoney,
        getFlightObject,
        getMinutesToString,
        getHourFromDateTimeString,
        isLocalHost
    } from '../api/utils.js';
    import * as DAO from "../api/apiCalls";
    import * as Sabre from '../api/Sabre.js';

    export default {
        name: 'Booking',
        components: {
            Datepicker,
        },
        data() {
            return {
                airLines: [],
                countries: [],
                flightData: {},
                searchData: {},
                adults: '1',
                childs: '0',
                infants: '0',
                form: {
                    adults: [{status: 'Mr', first_name: '', last_name: '', dob: '', nationality: ''}],
                    childs: [{status: 'Mr', first_name: '', last_name: '', dob: '', nationality: ''}],
                    infants: [{status: 'Mr', first_name: '', last_name: '', dob: '', nationality: ''}],
                    email: '',
                    confirm_email: '',
                    phone: '',
                    other: '',
                },
            }
        },

        beforeCreate() {
            DAO.getAirlines().then(response => {
                this.airLines = response.data;
            });
            DAO.getCountries().then(response => {
                this.countries = response.data;
            });
        },
        beforeMount() {
            this.flightData = this.$store.getters.getFlight;
            this.searchData = this.$store.getters.getSearch;
            this.adults = parseInt(this.searchData.adults, 10);
            this.childs = parseInt(this.searchData.childs, 10);
            this.infants = parseInt(this.searchData.infants, 10);
        },
        computed: {
            passengerInfo: function () {
                let list = [];
                this.form.adults.forEach(function (o) {
                    if (o.first_name !== '' && o.last_name !== '' && o.dob !== '' && o.nationality !== '') {
                        list.push(o);
                    }
                });
                this.form.childs.forEach(function (o) {
                    if (o.first_name !== '' && o.last_name !== '' && o.dob !== '' && o.nationality !== '') {
                        list.push(o);
                    }
                });
                this.form.infants.forEach(function (o) {
                    if (o.first_name !== '' && o.last_name !== '' && o.dob !== '' && o.nationality !== '') {
                        list.push(o);
                    }
                });
                return list;
            }
        },
        methods: {
            getDDMMMYYYY,
            addDaysToDate,
            minusDaysToDate,
            formatDate,
            getTime,
            getDifference,
            getMinutesToString,
            ensureTwoDigits,
            formatMoney,
            getStopInfo(names) {
                if (names.length === 0)
                    return "Non-Stop";
                return names.length + " Stop";
            },
            getLayOver: function (schedules, index) {
                try {
                    let count = schedules.length;
                    if ((index + 1) === count) {
                        return "";
                    } else {
                        let next = index + 1;
                        let time = getDifference(schedules[next].departureDateTime, schedules[index].arrivalDateTime);
                        let city = schedules[index].arrivalCity;
                        return `<div class="info"><b>Layover</b>&nbsp;${time}&nbsp;|&nbsp;${city}</div>`;
                    }
                } catch (e) {
                    return "";
                }
            },
            getAirportName(airportCode) {
                let airportCodeLC = airportCode.toLowerCase();
                let arr = this.$store.getters.getAirport.filter(o => o.iata !== null);
                let airports = arr.filter(o => o.iata.toLowerCase() === airportCodeLC);
                if (airports.length > 0) {
                    return airports[0].name;
                }
                return airportCode;
            },
            getFlightName(flightCode) {
                let match = this.airLines.filter(o => o.Code.toLowerCase() === flightCode.trim().toLowerCase());
                if (match.length > 0) {
                    return match[0].Name;
                }
                return flightCode;
            },
            getCountryName(countryCode) {
                let match = this.countries.filter(o => o.value.toLowerCase() === countryCode.toLowerCase());
                if (match.length > 0) {
                    return match[0].displayText;
                }
                return countryCode;
            },
            getUniqueId: function (prefix, outerIndex, innerIndex) {
                return prefix + '-' + (outerIndex + 1) + '-' + (innerIndex + 1);
            },
            submitFormData: function (e) {
                e.preventDefault();
                let queryObject = this.$store.getters.getSearch;
                let segmentInfo = this.$store.getters.getFlight;
                console.log(queryObject);
                console.log(segmentInfo);
                let pnrData = {
                    "from": queryObject.from,
                    "to": queryObject.to,
                    "AirlineCode": segmentInfo.FlightCode,
                    "ArrivalDT": segmentInfo.summary.legs[0].arrPoint.arrivalDateTime,
                    "DepartureDT": segmentInfo.summary.legs[0].depPoint.departureDateTime,
                    "FlightNumber": segmentInfo.summary.legs[0].depPoint.flightNumber.toString(),
                    "passengers": this.passengerInfo,
                    "email": this.form.email,
                    "phone": this.form.phone,
                    "other": this.form.other
                };

                if (isLocalHost) {
                    let dummyData = JSON.parse('{"from":"LHE","to":"DXB","passengers":[{"status":"Mr","first_name":"Shafqat","last_name":"Ali","dob":"1985-02-28T12:07:00.000Z","nationality":"Pakistan"}],"email":"shafqatali28@gmail.com","phone":"3217878919","other":"no special request."}');
                    Sabre.createPNR(dummyData);
                } else {
                    console.log("calling for PNR");
                    let resp = Sabre.createPNR(pnrData);
                    /*if (resp) {
                        let _this = this;
                        setTimeout(function () {
                            _this.$router.push("/thanks");
                        }, 10000);
                    }*/
                }
            },
        },
    }
</script>
